<template>
    <div>
        <LjsTreeTable :border="true" :datas="datas" :columns="columns" :debug="true"
                      style="width: 100%;height: 500px;"/>
    </div>
</template>

<script>
  import LjsTreeTable from '../components/LjsTreeTable/LjsTreeTable.vue'

  export default {
    name: 'LoadLocalData',
    components: {LjsTreeTable},
    data () {
      return {
        columns: [
          {
            key: 'id',
            label: '编号',
            resize: true
          },
          {
            key: 'name',
            label: '名称',
            resize: true
          },
          {
            key: 'sex',
            label: '性别',
            resize: true
          },
          {
            key: 'age',
            label: '年龄',
            resize: true
          },
          {
            key: 'gard',
            label: '年级',
            resize: true
          }
        ],
        datas: []
      }
    },
    mounted () {
      let datas = []
      for (let i = 0; i < 100; i++) {
        datas.push({
          id: i,
          name: 'name' + i,
          gard: parseInt((Math.random() * 8 + 1) + '') + '年级',
          age: parseInt((Math.random() * 50 + 20) + '') + '岁',
          sex: Math.random() > 0.5 ? '男' : '女'
        })
        this.datas = datas
      }
    }
  }
</script>

<style scoped>

</style>


